// 路由配置
import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import routes from './routes'
// 重写 push | replace
// 第一个参数：告诉原来的push方法，你往哪里跳转（传递哪些参数）
// 第二个参数：成功回调
// 第三个参数：失败的回调
// call | apply区别
// 相同点：都可以调用函数一次，都可以篡改函数的上下文一次
// 不同点：call与apply传递参数：call传递参数用逗号隔开，apply方法传数组
let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;
VueRouter.prototype.push = function(location, onComplete, onAbort) {
    if (onComplete && onAbort) {
        originPush.call(this, location, onComplete, onAbort);
    } else {   
        originPush.call(this, location, ()=>{}, () => {})
    }
}
VueRouter.prototype.replace = function(location, onComplete, onAbort) {
    if (onComplete && onAbort) {
        originReplace.call(this, location, onComplete, onAbort);
    } else {
        originReplace.call(this, location, ()=>{}, () => {})
    }
}
/*
    这段代码是对VueRouter的原型方法push和replace进行了重写。在重写后的方法中，首先保存了原始的push和replace方法，
    在新的方法中判断了传入的参数onComplete和onAbort是否存在，如果存在则调用原始的push和replace方法，如果不存在则将其设置为一个空函数。
    这样做的目的是为了在调用push和replace方法时，不需要传入onComplete和onAbort参数，如果不传入则会默认执行空函数，避免了调用时需要传入
    不必要的参数。这样可以简化调用方法，提高代码的可读性和易用性。
*/
export default new VueRouter({
    routes,
    // 滚动行为
    scrollBehavior(to, from, savedPosition) {
        // return { x: 0,y: 0 } 返回的这个x，y是坐标
        return { y: 0 }
    }
})