import Home from '@/views/MyHome'
import Login from '@/views/MyLogin'
import Register from '@/views/MyRegister'
export default [
    {
        name: "login",
        path: '/Login',
        component: Login,
        meta: {}
    },
    {
        name: "home",
        path: '/Home',
        component: Home,
        meta: {}
    },
    {
        name: "register",
        path: '/Register',
        component: Register,
        meta: {}
    },
    // 重定向，在项目跑起来的时候，访问/, 立马让他重定向到首页
    {
        path:"/",
        redirect: "/Home"
    }
]