<template>
  <div>
    <p @click="login">home</p>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      userInfoApi: "http://127.0.0.1:8010/my/userinfo", // 通过用户ID登录接口
    };
  },
  methods: {
    login() {
      const token = localStorage.getItem("token") || "";
      const headers = {
        Authorization: token,
      };
      this.$ajax({
        method: "post",
        url: this.userInfoApi,
        headers: headers
      })
        .then((response) => {
          console.log("得到的数据是", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

