<template>
    <!--  注册-->
    <div class="login_box">
      <a>
        <div class="login_close"></div>
      </a>
      <div class="login_panel">
        <div class="login_title">
          <img src="../../assets/img/logo.png" alt="">
          <p>注册新账号</p>
        </div>
        <label style="margin-top: 50px">昵称：</label>
        <input v-model="nickname" type="text" title="请输入昵称">
        <label>账号：</label>
        <input v-model="username" type="text" pattern="^\d{11}$" title="请输入账号">
        <label>密码：</label>
        <input v-model="userPassword" type="password" title="请输入密码">
        <input class="bt" @click="login" type="submit" value="注册">
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'MyRegister',
    data () {
      return {
        username: '',
        userPassword: '',
        nickname: '',
        userInfoApi: 'http://127.0.0.1:8010/api/reguser'
      }
    },
    methods: {
      login () {
        this.$ajax({
          method: 'post',
          url: this.userInfoApi,
          data: this.qs.stringify({
            username: this.username,
            password: this.userPassword,
            nickname: this.nickname,
          })
        }).then((response) => {
          console.log("得到的数据是" , response.data)
          if (response.data.message == '注册成功！') {
            console.log(response.data);
            this.$message({
              message: '注册成功！',
              type: 'success'
            })
            this.$bus.isShow = false; 
            setTimeout(() => {
              this.$router.push({ name: "home" })
            }, 1000)
          } else {
            this.$message.error(response.data.message)
          }
          console.log(response.data)
        }).catch((error) => {
          this.$message.error('注册异常，请稍后再试!')
          console.log(error)
        })
      }
    }
  }
  </script>
  
  <style scoped>
  
    /*登录框*/
    .login_box {
      z-index: 99;
      position: absolute;
      width: 380px;
      height: 580px;
      top: 50%;
      left: 50%;
      margin-left: -190px;
      margin-top: -270px;
      border-radius: 6px;
      background-color: #fff;
      box-shadow: 0 2px 10px #999;
    }
  
    .login_close {
      position: absolute;
      top: 0;
      right: 0;
      width: 64px;
      height: 64px;
      background: url(../../assets/img/qrcode.png) no-repeat right top;
      background-size: 100% 100%;
      border-top-right-radius: 5px;
      cursor: pointer;
      z-index: 99;
    }
  
    /*登录*/
    .login_panel {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 270px;
      height: 540px;
      padding: 0 55px;
      transform: translate(-50%, -50%);
      /* background: #fff; */
      border-radius: 6px;
      overflow: hidden;
    }
  
    .login_panel .login_title {
      text-align: center;
    }
  
    .login_panel .login_title img {
      margin-top: 60px;
      height: 70px;
      width: 70px;
      border-radius: 50%;
      padding: 10px;
      border: 3px solid #d7e8fc;
    }
  
    .login_panel .login_title p {
      margin-top: 15px;
      color: #999999;
      font-size: 15px;
    }
  
    .login_panel label {
      display: block;
      font-size: 12px;
      line-height: 18px;
      color: #a9a8a5;
      margin-top: 10px;
    }
  
    .login_panel input {
      display: inline;
      height: 42px;
      padding: 0 5%;
      line-height: 42px;
      font-size: 14px;
      color: #333333;
      border-radius: 4px;
      outline: 0;
      border: 0;
      width: 90%;
      background: #d7e8fc;
    }
  
    /* 按钮 */
    .login_panel .bt {
      margin-top: 35px;
      width: 100%;
      color: #ffffff;
      background: #379df6;
      cursor: pointer;
    }
  
    .login_panel .bt:hover {
      background-color: #2f86f6;
    }
  
  </style>
  